@font-face {
  font-family: "Inter";
  src: url("/fonts/Inter-Regular.eot") format("embedded-opentype"), url("/fonts/Inter-Regular.ttf") format("truetype"), url("/fonts/Inter-Regular.woff") format("woff"), url("/fonts/Inter-Regular.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("/fonts/Inter-Medium.eot") format("embedded-opentype"), url("/fonts/Inter-Medium.ttf") format("truetype"), url("/fonts/Inter-Medium.woff") format("woff"), url("/fonts/Inter-Medium.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 600;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("/fonts/Inter-SemiBold.eot") format("embedded-opentype"), url("/fonts/Inter-SemiBold.ttf") format("truetype"), url("/fonts/Inter-SemiBold.woff") format("woff"), url("/fonts/Inter-SemiBold.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("/fonts/Inter-Bold.eot") format("embedded-opentype"), url("/fonts/Inter-Bold.ttf") format("truetype"), url("/fonts/Inter-Bold.woff") format("woff"), url("/fonts/Inter-Bold.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 900;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  color: #18216D;
  text-decoration: none;
}

.sectionInner {
  margin: 0 auto;
  width: 1200px;
}

.sectionFirst {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: transparent url(../img/png/bgFirst.png) -15% 250% no-repeat;
  margin-bottom: 122px;
}
.sectionFirst_header {
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 18px;
  margin: 35px 0 60px 0;
}
.sectionFirst_header .sectionInner {
  margin: 0 auto;
  width: 1400px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionFirst_header-Logo {
  width: 114px;
  height: 32px;
}
.sectionFirst_header-Nav {
  flex-grow: 2;
  margin-left: 110px;
  width: 507px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.sectionFirst_header-Nav a {
  margin-right: 60px;
}
.sectionFirst_header-signInDownload .signIn {
  margin-right: 45px;
}
.sectionFirst_header-signInDownload .btn {
  width: 135px;
  height: 50px;
  border: none;
  outline: none;
  background-color: #18216D;
  color: #fff;
  border-radius: 6px;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 17px;
}
.sectionFirst_main .sectionInner {
  height: 519px;
  background: transparent url(../img/svg/firstImg.svg) center right no-repeat;
  background-size: contain;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.sectionFirst_main-text {
  width: 500px;
}
.sectionFirst_main-text .title {
  color: #18216D;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 48px;
  margin-bottom: 16px;
}
.sectionFirst_main-text .text {
  color: #18216D;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 17px;
  margin-bottom: 34px;
}
.sectionFirst_main-text .btn_green {
  width: 180px;
  height: 66px;
  border: none;
  outline: none;
  background-color: #00ECD0;
  color: #18216D;
  border-radius: 6px;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 17px;
}

.sectionCollaborate {
  margin-bottom: 130px;
}
.sectionCollaborate .sectionInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.sectionCollaborate_textAndRating {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}
.sectionCollaborate_textAndRating-text {
  width: 500px;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 36px;
  color: #18216D;
  line-height: 50px;
}
.sectionCollaborate_textAndRating-rating {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.sectionCollaborate_textAndRating-ratingItem {
  margin-right: 80px;
  width: 137px;
  height: 134px;
}
.sectionCollaborate_textAndRating-ratingItem .number {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  font-size: 60px;
  color: #18216D;
  margin-bottom: 10px;
}
.sectionCollaborate_textAndRating-ratingItem .textDecor {
  margin-bottom: 12px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.sectionCollaborate_textAndRating-ratingItem .textDecor img {
  width: 15px;
  height: 15px;
  margin-right: 5px;
}
.sectionCollaborate_textAndRating-ratingItem .text {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #18216D;
}
.sectionCollaborate_logoList {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.sectionFeatures .sectionInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
}
.sectionFeatures_titleText {
  width: 542px;
  color: #18216D;
}
.sectionFeatures_titleText-title {
  text-align: center;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 48px;
  margin-bottom: 12px;
}
.sectionFeatures_titleText-text {
  text-align: center;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  margin-bottom: 50px;
}
.sectionFeatures_featuresList {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionFeatures_featuresList-item {
  width: 320px;
  height: 267px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  color: #18216D;
}
.sectionFeatures_featuresList-itemTitle {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 24px;
}
.sectionFeatures_featuresList-itemText {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  opacity: 0.6;
  line-height: 26px;
}
.sectionFeatures_featuresList-itemLearnMore {
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 20px;
}